<template>
  <div class="score-container">
    <div class="show-score">
      <h1 class="score-number">&nbsp;{{scoreNumber}} 分!</h1>
      <p class="score-appraise">{{scoreAppraise()}}</p>
    </div>
    <div class="code-picture">
      <p class="code-info">关注葡萄之家，获取答案。</p>
      <div class="code-picture-container"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ScorePage',
  data() {
    return {
      correctAnswers: [2, 6, 11, 16, 17],
      scoreAppraises: [
        '什么？有没有认真答题！', // 0分
        '文化课是体育老师教的吧？', // 20分
        '看来你把知识都还给老师了。', // 40分
        '不要嘚瑟还有进步的空间！', // 60分
        '智商离爆表只差一步了！', // 80分
        '太聪明啦，葡萄之家欢迎你！' // 100分
      ]
    }
  },
  computed: {
    scoreNumber() {
      let score = 0
      const answers = this.$store.state.answerId
      answers.forEach((item, index) => {
        if (item === this.correctAnswers[index]) {
          score += 20
        }
      })
      return score
    }
  },
  methods: {
    scoreAppraise() {
      const num = parseInt(this.scoreNumber / 20)
      return this.scoreAppraises[num]
    }
  }
}
</script>

<style lang="less">
.show-score {
  position: absolute;
  top: 10vw;
  left: 50%;
  transform: translateX(-50%);
  width: 90vw;
  height: 90vw;
  background: url(@/images/scoreBanner.png) no-repeat center/80% 80%;
  .score-number {
    position: absolute;
    top: 45vw;
    left: 50%;
    color: red;
    font-size: 1.8rem;
    font-weight: 550;
    transform: translateX(-50%);
  }
  .score-appraise {
    position: absolute;
    top: 62vw;
    left: 50%;
    width: 80%;
    // background-color: yellow;
    transform: translateX(-48%);
    font-size: 0.75rem;
    text-align: center;
  }
}
.code-picture {
  position: absolute;
  top: 100vw;
  width: 100vw;
  height: 60vw;
  // background-color: #fff;
  .code-info {
    margin: 1rem 0 0 0.65rem;
    text-align: center;
    font-size: 0.7rem;
  }
  .code-picture-container {
    width: 70%;
    height: 70%;
    background-color: red;
    margin: 0.5rem auto;
    background: url(@/images/QRcode.png) no-repeat center/60%;
  }
}
</style>
